export default "\"use strict\";\n\n(function () {\n  if (typeof self === 'undefined' || !self.Prism || !self.document) {\n    return;\n  }\n\n  if (!Prism.plugins.toolbar) {\n    console.warn('Copy to Clipboard plugin loaded before Toolbar plugin.');\n    return;\n  }\n\n  var ClipboardJS = window.ClipboardJS || undefined;\n\n  if (!ClipboardJS && typeof require === 'function') {\n    ClipboardJS = require('clipboard');\n  }\n\n  var callbacks = [];\n\n  if (!ClipboardJS) {\n    var script = document.createElement('script');\n    var head = document.querySelector('head');\n\n    script.onload = function () {\n      ClipboardJS = window.ClipboardJS;\n\n      if (ClipboardJS) {\n        while (callbacks.length) {\n          callbacks.pop()();\n        }\n      }\n    };\n\n    script.src = 'https://cdnjs.cloudflare.com/ajax/libs/clipboard.js/2.0.0/clipboard.min.js';\n    head.appendChild(script);\n  }\n\n  Prism.plugins.toolbar.registerButton('copy-to-clipboard', function (env) {\n    var linkCopy = document.createElement('button');\n    var clip = null;\n    linkCopy.textContent = 'Copy'; //add\n\n    Prism.hooks.add(\"complete\", function (event) {\n      ClipboardJS && registerClipboard(event);\n    });\n\n    if (!ClipboardJS) {\n      callbacks.push(registerClipboard);\n    } else {\n      registerClipboard();\n    }\n\n    return linkCopy;\n\n    function registerClipboard(event) {\n      try {\n        clip && clip.destroy();\n      } catch (e) {\n        console.log(e);\n      }\n\n      clip = new ClipboardJS(linkCopy, {\n        'text': function text() {\n          return (event || env).code;\n        }\n      });\n      clip.on('success', function () {\n        linkCopy.textContent = 'Copied!';\n        resetText();\n      });\n      clip.on('error', function () {\n        linkCopy.textContent = 'Press Ctrl+C to copy';\n        resetText();\n      });\n    }\n\n    function resetText() {\n      setTimeout(function () {\n        linkCopy.textContent = 'Copy';\n      }, 5000);\n    }\n  });\n})();";