export default "/**\n * prism.js Funky theme\n * Based on Polyfilling the gaps talk slides http://lea.verou.me/polyfilling-the-gaps/\n * @author Lea Verou\n */\n\ncode[class*=\"language-\"],\npre[class*=\"language-\"] {\n\tfont-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;\n\tfont-size: 1em;\n\ttext-align: left;\n\twhite-space: pre;\n\tword-spacing: normal;\n\tword-break: normal;\n\tword-wrap: normal;\n\tline-height: 1.5;\n\n\t-moz-tab-size: 4;\n\t-o-tab-size: 4;\n\ttab-size: 4;\n\n\t-webkit-hyphens: none;\n\t-moz-hyphens: none;\n\t-ms-hyphens: none;\n\thyphens: none;\n}\n\n/* Code blocks */\npre[class*=\"language-\"] {\n\tpadding: .4em .8em;\n\tmargin: .5em 0;\n\toverflow: auto;\n\tbackground: url('data:image/svg+xml;charset=utf-8,<svg%20version%3D\"1.1\"%20xmlns%3D\"http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg\"%20width%3D\"100\"%20height%3D\"100\"%20fill%3D\"rgba(0%2C0%2C0%2C.2)\">%0D%0A<polygon%20points%3D\"0%2C50%2050%2C0%200%2C0\"%20%2F>%0D%0A<polygon%20points%3D\"0%2C100%2050%2C100%20100%2C50%20100%2C0\"%20%2F>%0D%0A<%2Fsvg>');\n\tbackground-size: 1em 1em;\n}\n\ncode[class*=\"language-\"] {\n\tbackground: black;\n\tcolor: white;\n\tbox-shadow: -.3em 0 0 .3em black, .3em 0 0 .3em black;\n}\n\n/* Inline code */\n:not(pre) > code[class*=\"language-\"] {\n\tpadding: .2em;\n\tborder-radius: .3em;\n\tbox-shadow: none;\n\twhite-space: normal;\n}\n\n.token.comment,\n.token.prolog,\n.token.doctype,\n.token.cdata {\n\tcolor: #aaa;\n}\n\n.token.punctuation {\n\tcolor: #999;\n}\n\n.token.namespace {\n\topacity: .7;\n}\n\n.token.property,\n.token.tag,\n.token.boolean,\n.token.number,\n.token.constant,\n.token.symbol {\n\tcolor: #0cf;\n}\n\n.token.selector,\n.token.attr-name,\n.token.string,\n.token.char,\n.token.builtin {\n\tcolor: yellow;\n}\n\n.token.operator,\n.token.entity,\n.token.url,\n.language-css .token.string,\n.token.variable,\n.token.inserted {\n\tcolor: yellowgreen;\n}\n\n.token.atrule,\n.token.attr-value,\n.token.keyword {\n\tcolor: deeppink;\n}\n\n.token.regex,\n.token.important {\n\tcolor: orange;\n}\n\n.token.important,\n.token.bold {\n\tfont-weight: bold;\n}\n.token.italic {\n\tfont-style: italic;\n}\n\n.token.entity {\n\tcursor: help;\n}\n\n.token.deleted {\n\tcolor: red;\n}\n\n/* Plugin styles: Diff Highlight */\npre.diff-highlight.diff-highlight > code .token.deleted:not(.prefix),\npre > code.diff-highlight.diff-highlight .token.deleted:not(.prefix) {\n\tbackground-color: rgba(255, 0, 0, .3);\n\tdisplay: inline;\n}\n\npre.diff-highlight.diff-highlight > code .token.inserted:not(.prefix),\npre > code.diff-highlight.diff-highlight .token.inserted:not(.prefix) {\n\tbackground-color: rgba(0, 255, 128, .3);\n\tdisplay: inline;\n}\n";