import React, { useEffect, useState } from 'react';
import ReactPrismEditor from 'react-prism-editor';
import Stomp from 'stompjs';
import { useNavigate } from 'react-router-dom';
import AceEditor from 'react-ace';
import '../css/editor.css'

import 'ace-builds/src-noconflict/mode-java';
import 'ace-builds/src-noconflict/theme-terminal';

const startUpCode =
    `public class OnlineIDE {
      
  public static void main(String[] args) {
    System.out.println("Hello World");
  }
}`;

const Editor = (props) => {
  
  const [code, setCode] = useState(startUpCode);
  const [ws, setWs] = useState(null);
  const [isConnected, setIsConnected] = useState(false);
  const [output, setOutput] = useState('');
  const [isContainerCreated, setIsContainerCreated] = useState(false);
  const navigate = useNavigate();

  const connect = () => {
    if (ws && ws.connected) {
      console.log('Already connected');
      props.setOutput('Already connected');
      return;
    }
    const stompClient = Stomp.client('ws://localhost:8080/socket');
    console.log('Connecting to WebSocket test');


    stompClient.connect({}, function (frame) {
      stompClient.subscribe('/editor/output', function (code) {
        console.log('Received code ' + code.body);
        props.setOutput(prevOutput => prevOutput + '\n' + code.body);
      });
      setWs(stompClient);
      props.setOutput('Connected to WebSocket');
    }, function (error) {
      console.error('STOMP error ' + error);
    });
  };

  const disconnect = () => {
    if (ws && ws.connected) {
      ws.disconnect();
      setWs(null);
      props.setOutput('Disconnected from WebSocket');
    } else {
      console.log('Already disconnected');
    }
  };

  const createContainer = () => {
    if (ws && ws.connected) {
      fetch('http://localhost:8080/handleDockerContainer', { method: 'POST' })
        .catch((error) => {
          console.error('Error:', error);
        });
      props.setOutput('Docker container created');
      setIsContainerCreated(true);
    } else {
      console.error('WebSocket is not connected');
      props.setOutput('Connect to websocket first');
    }

  };

  const submit = () => {
    if (ws && ws.connected) {
      ws.send("/app/editor", {}, JSON.stringify({ 'code': code }));
      console.log('Code submitted successfully');
    } else {
      console.error('WebSocket is not connected');
    }
  };

  const returnHome = async () => {
    navigate('/');
  };
  
  return (
    <>
      <div>
        <AceEditor 
          mode="java"
          theme="terminal"
          value={code}
          onChange={setCode}
          name="deneme"
          className='code-editor'
          editorProps={{ $blockScrolling: true }}
          setOptions={{
            enableBasicAutocompletion: true,
            enableLiveAutocompletion: true,
            enableSnippets: true,
            showLineNumbers: true,
            tabSize: 2,
            fontSize: 20,
            minLines: 50,
          }}
        />
      </div>
      <div className='buttons'>
        <button id="button" onClick={connect}>Connect to Websocket</button>
        <button id="button" onClick={disconnect}>Disconnect from Websocket</button>
        <button id="button" onClick={createContainer}>Create Docker Container</button>
        <button id="button" onClick={submit}>Submit</button>
        <button id="button" onClick={returnHome}>Return</button>
      </div>
      
    </>
  );
}

export default Editor;